import { LayoutActionTypes, LayoutActions } from './layout.actions';

export function layoutReducer(
  state: LayoutState = initialLayoutState,
  action: LayoutActions): LayoutState {
  switch (action.type) {
    case LayoutActionTypes.SHOW_DIALOG: {
      return { ...state, isDialogOpen: true };
    }

    case LayoutActionTypes.HIDE_DIALOG: {
      return { ...state, isDialogOpen: false };
    }

    case LayoutActionTypes.SHOW_DRAWER: {
      return { ...state, isDrawerOpen: true };
    }

    case LayoutActionTypes.HIDE_DRAWER: {
      return { ...state, isDrawerOpen: false };
    }

    case LayoutActionTypes.SHOW_NOTIFICATION: {
      return state;
    }

    case LayoutActionTypes.HIDE_NOTIFICATION: {
      return state;
    }

    default:
      return state;
  }
}

export interface LayoutState {
  isLoading: boolean;
  isDrawerOpen: boolean;
  isDialogOpen: boolean;
}

export const initialLayoutState = {
  isLoading: false,
  isDrawerOpen: false,
  isDialogOpen: false
};

export const selectorLayout = state =>
    <LayoutState>(state.layout || initialLayoutState);


