import { Effect, Actions } from '@ngrx/effects';
import { Store, Action } from '@ngrx/store';
import { MatDialog, MatDialogConfig, MatDialogRef } from '@angular/material';
import { Injectable, Inject } from '@angular/core';
import { LayoutActionTypes, ShowDialogAction,
  HideDialogAction, ShowDrawerAction, HideDrawerAction,
  ShowNotificationAction, HideNotificationAction } from './layout.actions';
import { tap } from 'rxjs/operators';
import { AUTH_SERVICE } from '../../auth/auth.module';
import { FirebaseAuthService } from '../../auth/firebase-auth.service';
import { DialogComponent } from '../default-dialog/default-dialog.component';
import { defer, pipe, Observable } from 'rxjs';
import { LayoutState } from './layout.reducer';
import { TdDialogService } from '@covalent/core';
import { AnimatedLoginComponent } from '../animated-login/animated-login.component';
import { Router } from '@angular/router';
import { DefaultLoginComponent } from '../default-login/default-login.component';

@Injectable({
  providedIn: 'root'
})
export class LayoutEffects {
  private dialogRef$: MatDialogRef<any>;
  @Effect({ dispatch: false })
  showDialog$: Observable<Action> = this.actions$.ofType(LayoutActionTypes.SHOW_DIALOG)
    .pipe(
      tap(action => {
        this.dialogRef$ = this._dialogService.open(DefaultLoginComponent, <MatDialogConfig>{
          autoFocus: false,
          panelClass: 'transparent-dialog'
        });

        this.dialogRef$.afterClosed();
      })
    );

  @Effect({ dispatch: false})
  hideDialog$: Observable<Action> = this.actions$.ofType(LayoutActionTypes.HIDE_DIALOG)
      .pipe(
        tap(action => this.dialogRef$.close())
      );
      // tap(action => {
      //     this._dialogService.openPrompt({
      //       message: 'This is how simple it is to create a prompt with this wrapper service. Prompt something.',
      //       disableClose: false, // defaults to false
      //       title: 'Prompt', // OPTIONAL, hides if not provided
      //       value: 'Prepopulated value', // OPTIONAL
      //       cancelButton: 'Cancel', // OPTIONAL, defaults to 'CANCEL'
      //       acceptButton: 'Ok', // OPTIONAL, defaults to 'ACCEPT'
      //       width: '400px', // OPTIONAL, defaults to 400px
      //     }).afterClosed().subscribe((newValue: string) => {
      //       if (newValue) {
      //         // DO SOMETHING
      //       } else {
      //         // DO SOMETHING ELSE
      //       }
      //     });
      // })

  constructor(private store$: Store<any>,
    private actions$: Actions<Action>,
    authSrv: FirebaseAuthService,
    private _dialogService: TdDialogService,
    private router: Router,
    private dialog$: MatDialog) {
  }
}


