import { Injectable, OnInit } from '@angular/core';
import { AngularFireAuth } from 'angularfire2/auth';
import { AuthProvider } from '@firebase/auth-types';

@Injectable({
  providedIn: 'root'
})
export class FirebaseAuthService implements OnInit {
  private static instance$: FirebaseAuthService;
  private user: any = null;
  public constructor(private srv: AngularFireAuth) {
  }

  private static geInstance$ = (auth: AngularFireAuth) => {
    FirebaseAuthService.instance$ =
    FirebaseAuthService.instance$ != null ? FirebaseAuthService.instance$ : new FirebaseAuthService(auth);
    return FirebaseAuthService.instance$;
  }

  static GetInstance() {
    return (auth: AngularFireAuth) => {
      FirebaseAuthService.instance$ =
      FirebaseAuthService.instance$ != null ? FirebaseAuthService.instance$ : new FirebaseAuthService(auth);
      return FirebaseAuthService.instance$;
    }
  }

  ngOnInit(): void {
    // throw new Error('Method not implemented.');
  }

  public login(creds: AuthProvider
    | { username: string, password: string}) {
      if ((creds as any).username) {
        return this.srv.auth.signInWithEmailAndPassword((<any>creds).username, (<any>creds).password);
      } else {
        return this.loginWithProvider(<AuthProvider>creds);
      }
  }

  public loginWithRole(creds: AuthProvider
    | { username: string, password: string }) {

  }


  public onAuthStateChanged(callback) {
    this.srv.authState.subscribe((state) => {
      callback(state);
      if (state) {
        this.user = state;
      } else {
        this.user = null;
      }
    });
  }

  public get isAuthenticated(): boolean {
    return this.user != null;
  }

  private loginWithProvider (provider) {
    return this.srv.auth.signInWithPopup(provider);
  }
}
