import { AuthActionTypes, SuccessAction } from './auth.actions';
import { Injectable, Inject } from '@angular/core';
import { Action, Store } from '@ngrx/store';
import { Actions, Effect } from '@ngrx/effects';
import { tap } from 'rxjs/operators';
import { ShowDialogAction, HideDialogAction } from '../../layout/state/layout.actions';
import { AUTH_SERVICE } from '../auth.module';
import { FirebaseAuthService } from '../firebase-auth.service';
import { MatSnackBar } from '@angular/material';
import { LocalStorageService, AUTH_KEY } from '../../core';

@Injectable()
export class AuthEffects {
  @Effect({ dispatch: false })
  beginAuthentication$ = () => {
    return this.actions$
      .ofType(AuthActionTypes.BEGIN_AUTHENTICATION)
      .pipe(
        tap((state: any) => {
          this.store$.dispatch(new ShowDialogAction({}));
        })
      );
  }

  @Effect({ dispatch: false })
  anonymous$ = () => {
    return this.actions$
      .ofType(AuthActionTypes.ANONYMOUS)
      .pipe(
        tap((state: any) => {
          // this.store$.dispatch({});
        })
      );
  }

  @Effect({ dispatch: false })
  login$ = () => {
    return this.actions$
      .ofType(AuthActionTypes.LOGIN)
      .pipe(
        tap((state: any) => {
          console.log(state);
          this.authSrv$.login(state.payload)
            .then(result => {
              this.store$.dispatch(new SuccessAction(result));
            });
        })
      );
  }

  @Effect({ dispatch: false })
  loginWithRole$ = () => {
    return this.actions$
      .ofType(AuthActionTypes.LOGIN_WITH_ROLE)
      .pipe(
        tap((state: any) => {
          // this.store$.dispatch({});
        })
      );
  }

  @Effect({ dispatch: false })
  success$ = () => {
    return this.actions$
      .ofType(AuthActionTypes.SUCCESS)
      .pipe(
        tap((state: any) => {
          this.localStorageService$.setItem(AUTH_KEY, { isAuthenticated: true });
          this.snackBar$.open('Authenticated');
          this.store$.dispatch(new HideDialogAction({}));
        })
      );
  }

  @Effect({ dispatch: false })
  fail$ = () => {
    return this.actions$
      .ofType(AuthActionTypes.FAIL)
      .pipe(
        tap((state: any) => {
          // this.store$.dispatch({});
        })
      );
  }

  @Effect({ dispatch: false })
  logout$ = () => {
    return this.actions$
      .ofType(AuthActionTypes.LOGOUT)
      .pipe(
        tap((state: any) => {
          // this.store$.dispatch({});
        })
      );
  }

  @Effect({ dispatch: false })
  result$ = () => {
    return this.actions$
      .ofType(AuthActionTypes.RESULT)
      .pipe(
        tap((state: any) => {
          // this.store$.dispatch({});
        })
      );
  }
  constructor (
    private store$: Store<any>,
    private actions$: Actions<Action>,
    private snackBar$: MatSnackBar,
    private localStorageService$: LocalStorageService,
    private authSrv$: FirebaseAuthService
  ) { }
}
