import { Injectable, Inject } from '@angular/core';
import { CanActivate } from '@angular/router';
import { Store } from '@ngrx/store';

import { selectorAuth } from './auth.reducer';
import { FirebaseAuthService } from '../../auth/firebase-auth.service';
import { AUTH_SERVICE } from '../../auth/auth.module';

@Injectable()
export class AuthGuardService implements CanActivate {
  isAuthenticated = false;

  constructor(private store: Store<any>,
    private authSrv: FirebaseAuthService) {
      authSrv.onAuthStateChanged((state) => {
        if (state) {
          this.isAuthenticated = true;
        } else { this.isAuthenticated = false; }
      });
    // this.store
    //   .select(selectorAuth)
    //   .subscribe(auth => (this.isAuthenticated = auth.isAuthenticated));
  }

  canActivate(): boolean {
    return this.isAuthenticated = this.authSrv.isAuthenticated;
  }
}
