import { AuthActions, AuthActionTypes } from './auth.actions';
import { initialState, AuthState} from './auth.interfaces';

export function authReducer
  (
    state: AuthState = initialState,
    action: AuthActions
  ) {
    switch (action.type) {
      case AuthActionTypes.ANONYMOUS: {
            return state;
          }
      case AuthActionTypes.BEGIN_AUTHENTICATION: {
            return state;
          }
      case AuthActionTypes.LOGIN: {
            return state;
          }
      case AuthActionTypes.LOGIN_WITH_ROLE: {
            return state;
          }
      case AuthActionTypes.SUCCESS: {
            return state;
          }
      case AuthActionTypes.FAIL: {
            return state;
          }
      case AuthActionTypes.RESULT: {
            return state;
          }
      default:
        return state;
    }
}

