import { Component, OnInit, AfterViewInit } from '@angular/core';
import { Store } from '@ngrx/store';
import { LoginAction } from '../../auth/state/auth.actions';
import { FormGroup, FormControl, Validators } from '@angular/forms';

declare var $: any;

@Component({
  selector: 'anms-default-login',
  templateUrl: './animated-login.component.html',
  styleUrls: ['./animated-login.component.scss']
})
export class DefaultLoginComponent implements AfterViewInit {
  containerClass = 'container';
  _panelOneClass = 'form-panel one';
  _panelTwoClass = 'form-panel two';
  awaitingResult = false;
  loginForm = new FormGroup({
    username: new FormControl('', [Validators.required, Validators.email]),
    password: new FormControl('', [Validators.minLength(6), Validators.required])
  });

  registerForm = new FormGroup({
    username: new FormControl('', [Validators.required, Validators.email]),
    password: new FormControl('', [Validators.minLength(6), Validators.required]),
    confirmPassword: new FormControl('', [Validators.minLength(6), Validators.required])
  });

  private toggle$ = false;
  constructor(private store$: Store<any>) {

  }

  ngAfterViewInit(): void {
    initJquery();
  }

  toggle() {
    this.toggle$ = !this.toggle$;
    this.panelOneClass();
    this.panelTwoClass();
  }

  panelOneClass() {
    return this._panelOneClass;
  }

  panelTwoClass() {
    return this._panelTwoClass;
  }

  formToggle() {
  }

  login() {
    const creds = this.loginForm.value;
    this.store$.dispatch(new LoginAction({ username: creds.username, password: creds.password }));
    this.awaitingResult = true;
  }

  register() {

  }

  active() {
    return this.containerClass = this.toggle$ ? 'container active' : 'container';
  }
}


export function initJquery() {
  $(document).ready(function() {
    const panelOne = $('.form-panel.two').height(),
      panelTwo = $('.form-panel.two')[0].scrollHeight;

    $('.form-panel.two').not('.form-panel.two.active').on('click', (e) => {
      e.preventDefault();

      $('.form-toggle').addClass('visible');
      $('.form-panel.one').addClass('hidden');
      $('.form-panel.two').addClass('active');
      $('.form').animate({
        'height': panelTwo
      }, 200);
    });

    $('.form-toggle').on('click', (e) => {
      e.preventDefault();
      $('.form-toggle').removeClass('visible');
      $('.form-panel.one').removeClass('hidden');
      $('.form-panel.two').removeClass('active');
      $('.form').animate({
        'height': panelOne
      }, 200);
    });
  });
}
