import { Action } from '@ngrx/store';

export enum AuthActionTypes {
  ANONYMOUS = '[Auth] Anonymous',
  BEGIN_AUTHENTICATION = '[Auth] Begin Authentication Process',
  LOGIN = '[Auth] Login',
  LOGIN_WITH_ROLE = '[Auth] Login With Role',
  SUCCESS = '[Auth] Success',
  FAIL = '[Auth] Fail',
  LOGOUT = '[Auth] Logout',
  RESULT = '[Auth] Result',
  REDIRECT = '[Auth] Redirect',
  REQUEST_TOKEN = '[Auth] Request Token',
  REFRESH_TOKEN = '[Auth] Refresh Token',
  RESULT_TOKEN = '[Auth] Result Token'
}

export class AnonymousAction implements Action {
  public readonly type = AuthActionTypes.ANONYMOUS;
  constructor(public payload: any) { }
}

export class BeginAuthenticationAction implements Action {
  public readonly type = AuthActionTypes.BEGIN_AUTHENTICATION;
  constructor(public payload: any) { }
}

export class LoginAction implements Action {
  public readonly type = AuthActionTypes.LOGIN;
  constructor(public payload: any) { }
}

export class LoginWihRoleAction implements Action {
  public readonly type = AuthActionTypes.LOGIN_WITH_ROLE;
  constructor(public payload: any) { }
}

export class SuccessAction implements Action {
  public readonly type = AuthActionTypes.SUCCESS;
  constructor(public payload: any) { }
}

export class FailAction implements Action {
  public readonly type = AuthActionTypes.FAIL;
  constructor(public payload: any) { }
}

export class LogoutAction implements Action {
  public readonly type = AuthActionTypes.LOGOUT;
  constructor(public payload: any) { }
}

export class ResultAction implements Action {
  public readonly type = AuthActionTypes.RESULT;
  constructor(public payload: any) { }
}

export class RedirectAction implements Action {
  public readonly type = AuthActionTypes.REDIRECT;
  constructor(public payload: any) { }
}

export class RequestTokenAction implements Action {
  public readonly type = AuthActionTypes.REQUEST_TOKEN;
  constructor(public payload: any) { }
}

export class RefreshTokenAction implements Action {
  public readonly type = AuthActionTypes.REFRESH_TOKEN;
  constructor(public payload: any) { }
}

export class ResultTokenAction implements Action {
  public readonly type = AuthActionTypes.RESULT_TOKEN;
  constructor(public payload: any) { }
}

export type AuthActions = AnonymousAction | LoginAction | LoginWihRoleAction | BeginAuthenticationAction
                    | SuccessAction | FailAction | LogoutAction | ResultAction | RedirectAction
                    | RequestTokenAction | RefreshTokenAction | ResultTokenAction;

