import { NgModule, ModuleWithProviders } from '@angular/core';
import { CommonModule } from '@angular/common';
import { LayoutComponent } from './default-layout/default-layout.component';
import { DialogComponent } from './default-dialog/default-dialog.component';
import { NavbarComponent } from './default-navbar/default-navbar.component';
import { FooterComponent } from './default-footer/default-footer.component';
import { MatDialogModule, MAT_DIALOG_DEFAULT_OPTIONS, MAT_DIALOG_DATA, MatSnackBar } from '@angular/material';
import { StoreModule } from '@ngrx/store';
import { EffectsModule } from '@ngrx/effects';
import { LayoutEffects } from './state/layout.effects';
import { layoutReducer } from './state/layout.reducer';
import { SharedModule } from '../shared';
import { RouterModule } from '@angular/router';
import { StoreDevtoolsModule } from '@ngrx/store-devtools';
import { AuthModule, AUTH_SERVICE } from '../auth/auth.module';
import { authReducer } from '../auth/state/auth.reducer';
import { AuthEffects } from '../auth/state/auth.effects';
import { FirebaseAuthService } from '../auth/firebase-auth.service';
import { AngularFireAuth } from 'angularfire2/auth';
import { TdMediaService, TdDialogService } from '@covalent/core';
import { DefaultDashboardComponent } from './default-dashboard/default-dashboard.component';
import { ChartCommonModule, AreaChartModule, BarChartModule, NgxChartsModule } from '@swimlane/ngx-charts';
import { DefaultLoginComponent } from './default-login/default-login.component';
import { AnimatedMenuComponent } from './animated-menu/animated-menu.component';
@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    RouterModule, // .forChild([{ path: 'login', component: DefaultLoginComponent}]),
    AuthModule,
    NgxChartsModule,
    AreaChartModule,
    BarChartModule,
    ChartCommonModule,
    StoreDevtoolsModule.instrument({
      maxAge: 5
    }),
    StoreModule.forRoot({
      layout: layoutReducer,
      auth: authReducer
    }),
    EffectsModule.forRoot([LayoutEffects, AuthEffects]),
    MatDialogModule
  ],
  declarations: [
    LayoutComponent,
    NavbarComponent, FooterComponent, DialogComponent, DefaultDashboardComponent, DefaultLoginComponent
  ],
  entryComponents: [DialogComponent],
  providers: [
    MatSnackBar,
    FirebaseAuthService,
    // { provide: AUTH_SERVICE, useValue: FirebaseAuthService.GetInstance(), deps: [AngularFireAuth] },
    { provide: MAT_DIALOG_DEFAULT_OPTIONS, useValue: { hasBackdrop: true } },
    { provide: MAT_DIALOG_DATA, useValue: { }}
  ]
})
export class LayoutModule {
  static forRoot(): ModuleWithProviders {
    return {
      ngModule: LayoutModule,
      providers: [
        AngularFireAuth,
        TdMediaService,
        TdDialogService,
        FirebaseAuthService,
        // { provide: AUTH_SERVICE,
        //   useValue: FirebaseAuthService.GetInstance(),
        //   deps: [AngularFireAuth] },
        { provide:
          MAT_DIALOG_DEFAULT_OPTIONS,
          useValue: { hasBackdrop: true } },
        { provide: MAT_DIALOG_DATA,
          useValue: { }}
      ]
    };
  }
}

