import { Action } from '@ngrx/store';

export enum LayoutActionTypes {
  SHOW_DIALOG = '[Layout] Show Dialog',
  HIDE_DIALOG = '[Layout] Hide Dialog',
  SHOW_DRAWER = '[Layout] Show Drawer',
  HIDE_DRAWER = '[Layout] Hide Drawer',
  SHOW_NOTIFICATION = '[Layout] Show Notification',
  HIDE_NOTIFICATION = '[Layout] Hide Notification'
}

export class ShowDialogAction implements Action {
  public readonly type = LayoutActionTypes.SHOW_DIALOG;
  constructor(public payload: any) {

  }
}

export class HideDialogAction implements Action {
  public readonly type = LayoutActionTypes.HIDE_DIALOG;
  constructor(public payload: any) {

  }
}

export class ShowDrawerAction implements Action {
  public readonly type = LayoutActionTypes.SHOW_DRAWER;
  constructor(public payload: any) {

  }
}

export class HideDrawerAction implements Action {
  public readonly type = LayoutActionTypes.HIDE_DRAWER;
  constructor(public payload: any) {

  }
}

export class ShowNotificationAction implements Action {
  public readonly type = LayoutActionTypes.SHOW_NOTIFICATION;
  constructor(public payload: any) {

  }
}

export class HideNotificationAction implements Action {
  public readonly type = LayoutActionTypes.HIDE_NOTIFICATION;
  constructor(public payload: any) {

  }
}

export type LayoutActions = ShowDialogAction | HideDialogAction
  | ShowDrawerAction | HideDrawerAction | ShowNotificationAction | HideNotificationAction;
